import React from "react";
import { Route, Switch } from "react-router-dom";
import Definitions from "./definitions";
import asyncComponent from "../../util/asyncComponent";

const Main = ({ match }) => (
  <Switch>
    <Route path={`${match.url}/definitions`} component={Definitions} />
    <Route path={`${match.url}/dashboard`} component={asyncComponent(() => import('../features/dashboard/index'))} />
  </Switch>
);

export default Main;
