import React from "react";
import {Redirect, Route, Switch} from "react-router-dom";
import asyncComponent from "util/asyncComponent";

const Definitions = ({match}) => (
  <Switch>
    <Redirect exact from={`${match.url}/`} to={`${match.url}/account`}/>
    <Route path={`${match.url}/account`} component={asyncComponent(() => import('./account/index'))}/>
    <Route path={`${match.url}/users`} component={asyncComponent(() => import('./users/index'))}/>
    <Route path={`${match.url}/web-sites`} component={asyncComponent(() => import('./web-sites/index'))}/>
  </Switch>
);

export default Definitions;
