import React, { Component } from "react";
import { connect } from "react-redux";
import { Avatar, Popover } from "antd";
import { userSignOut } from "appRedux/actions/Auth";
import IntlMessages from "../../util/IntlMessages";

class UserProfile extends Component {

  render() {
    const userMenuOptions = (
      <ul className="gx-user-popover">
        <li><IntlMessages id="sidebar.my-account" /></li>
        <li><IntlMessages id="sidebar.inbox" /></li>
        <li onClick={() => this.props.userSignOut()}>
        <IntlMessages id="sidebar.logout" />
        </li>
      </ul>
    );

    return (

      <div className="gx-flex-row gx-align-items-center gx-mb-2 gx-avatar-row">
        <Popover
          placement="bottomRight"
          content={userMenuOptions}
          trigger="click"
        >
          <Avatar
            src='https://www.ecozum.com.tr/wp-content/uploads/2018/03/ecozum-favicon.png'
            className="gx-size-40 gx-pointer gx-mr-3"
            alt=""
          />
          <span className="gx-avatar-name">
            Alihan Berk Bilgiç
            <i className="icon icon-chevron-down gx-fs-xxs gx-ml-2" />
          </span>
        </Popover>
      </div>

    )

  }
}

export default connect(({ settings }) => settings, { userSignOut })(UserProfile);
